import React, { useState } from "react";
// We import React Icons, just like before
import { FaSignOutAlt, FaCog, FaUserCircle, FaRegCopy } from "react-icons/fa";

// --- Sub-component for Word History ---
const WordHistory = ({ history }) => (
  // We use `bg-line` which maps to var(--color-line)
  <div className="bg-line rounded-lg p-6 shadow-lg">
    <h3 className="mt-0 mb-4 text-xl font-semibold text-fg border-b border-comment/50 pb-2">
      Word History
    </h3>
    <ul className="list-none p-0 m-0 max-h-[200px] overflow-y-auto text-comment">
      {history.map((word, index) => (
        <li key={index} className="py-1 font-mono">
          â–¶ {word}
        </li>
      ))}
    </ul>
  </div>
);

// --- Sub-component for Player List ---
const PlayerList = ({ players }) => {
  const [copied, setCopied] = useState(false);

  const handleCopyInvite = () => {
    const inviteLink = "https://your-game-link.com/invite/12345";
    navigator.clipboard.writeText(inviteLink).then(() => {
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    });
  };

  return (
    // We use `bg-line` for the card background
    <div className="bg-line rounded-lg p-6 shadow-lg">
      <h3 className="mt-0 mb-4 text-xl font-semibold text-fg border-b border-comment/50 pb-2">
        Players in Room
      </h3>
      <ul className="list-none p-0 m-0 flex flex-col gap-3">
        {players.map((player, index) => (
          <li key={player.id} className="flex items-center gap-3 text-base">
            <span>{index + 1}.</span>
            {player.avatar ? (
              <img
                src={player.avatar}
                alt={player.name}
                className="w-6 h-6 rounded-full"
              />
            ) : (
              <FaUserCircle className="text-comment" size={24} />
            )}
            <span className="text-fg">
              {player.name}{" "}
              {player.isYou && (
                <strong className="font-semibold text-purple">(You)</strong>
              )}
            </span>
          </li>
        ))}
        {/* Placeholder for empty slot */}
        {players.length < 4 && (
          <li className="flex items-center gap-3 text-base">
            <span>{players.length + 1}.</span>
            <FaUserCircle className="text-comment" size={24} />
            <span className="text-comment italic">Waiting...</span>
          </li>
        )}
      </ul>

      {/* This button uses `bg-purple` and `text-bg` (for contrast) 
        from your theme variables. 
      */}
      <button
        className="flex items-center justify-center gap-2 w-full py-3 px-4 mt-6 text-base font-semibold text-bg bg-purple rounded-md cursor-pointer transition-opacity hover:opacity-80"
        onClick={handleCopyInvite}
      >
        <FaRegCopy />
        <span>{copied ? "Copied!" : "Copy Invite Link"}</span>
      </button>
    </div>
  );
};

// --- Main GameRoom Component ---
const GameRoom = () => {
  // Data is the same as before
  const players = [
    { id: 1, name: "DemoUser_123", avatar: null, isYou: true },
    { id: 2, name: "Guest_789", avatar: null, isYou: false },
    { id: 3, name: "Guest_456", avatar: null, isYou: false },
  ];

  const wordHistory = [
    "START",
    "START",
    "APPLE",
    "EAGLE",
    "BOARD",
    "BOARD",
    "DRAGON",
    "NIGHT",
    "TIGER",
  ];

  const currentWord = "KEYBOARD";
  const startingLetter = "D";
  const playerCount = players.length;
  const maxPlayers = 4;

  // The `body` will have the background color from your CSS.
  // This container just sets up the flex layout.
  return (
    <div className="flex flex-col min-h-screen w-full text-fg">
      <header className="flex justify-between items-center p-6 border-b border-line">
        {/* This status bar uses `bg-line` which is the same as the cards.
          The image had a different purple, but `tokyo` theme doesn't provide it.
        */}
        <div className="text-lg font-medium bg-line py-2 px-4 rounded-lg">
          Waiting for players... {playerCount} / {maxPlayers}
        </div>
        <div className="flex gap-4">
          <button className="text-comment hover:text-fg transition-colors">
            <FaCog size={20} />
          </button>
          <button className="text-comment hover:text-fg transition-colors">
            <FaSignOutAlt size={20} />
          </button>
        </div>
      </header>

      <main className="flex flex-1 p-8 gap-8">
        <aside className="flex-shrink-0 w-[300px] flex flex-col gap-6">
          <WordHistory history={wordHistory} />
          <PlayerList players={players} />
        </aside>

        <section className="flex-1 flex flex-col justify-center items-center bg-line rounded-lg p-8 shadow-lg">
          <h1 className="text-8xl font-bold text-fg uppercase tracking-wider">
            {currentWord}
          </h1>
          <p className="text-2xl text-comment mt-2">
            Starts with: {startingLetter}
          </p>
        </section>
      </main>

      {/* Bottom 'N' Icon */}
      <div className="fixed bottom-6 left-6 bg-line text-fg font-bold w-10 h-10 flex items-center justify-center rounded-full border border-comment/50">
        N
      </div>
    </div>
  );
};

export default GameRoom;
